@charset "utf-8";

// ==================================
//
// ==================================

//  -----------------------
	$colorType01: #F7F4E6;	//		
	$colorType02: #E78F81;	//		
	$colorType03: #59B1F4;	//	
	$colorType04: #916AB5;	//2	
	$colorType05: #E04F38;	//			
	$colorType06: #FFCFB3;	//		
	$colorType07: #B7E0FF;	//		
	$colorType08: #7841AC;	//		
	$colorType09: #E1DDC8;	//		

	$color-link: #808080;	//
	$color-link-l: #ccc;	//
	$color-link-d: #000;	//

//  -----------------------
	$sansSerif: "","Meiryo"," ","Hiragino Kaku Gothic Pro"," Pro W3",sans-serif;
	$serif: "", YuMincho, " ProN W3", "Hiragino Mincho ProN", "HGE", " ", " ", serif;

	$font-weights: (
		Light:300,
		Regular:400,
		Medium:500,
		SemiBold:600,
		Bold:700,
		ExtraBold:800,
		Black:900,
	);
	@mixin fontType01($font-weight){
		font-family: "Noto Sans JP", serif;
		font-optical-sizing: auto;
		font-weight: map-get($font-weights,$font-weight);
		font-style: normal;
	}
	@mixin fontType02($font-weight){
		font-family: "Zen Maru Gothic", serif;
		font-weight: map-get($font-weights,$font-weight);
		font-style: normal;
	}


//  -----------------------
	$spaceSize: 1.8rem;

//  -----------------------
	$breakpoints: (
		xs:"print, screen and (min-width: 321px)",
		s:"print, screen and (min-width: 576px)",
		m:"print, screen and (min-width: 768px)",
		l:"print, screen and (min-width: 992px)",
		xl:"print, screen and (min-width: 1280px)",
		xxl:"print, screen and (min-width: 1600px)",
		xxxl:"print, screen and (min-width: 1800px)",
	);
	@mixin media($breakpoint){
		@media #{map-get($breakpoints,$breakpoint)}{
			@content;
		}
	}

// z-index  -----------------------
	$layer: (
		modal: 999,
		header: 20,
		tooltip: 10,
		default: 1
	);

// pxem  16-----------------------
	@function em($px, $base: 16) {
		$value: $px;
		$value: calc($px/$base) * 1em;
		@return $value;
	}

//  -----------------------
	$pageNameList: company, contact;